.tour-container {
  display: grid;
  grid-template-areas: "slider slider" "map map"   "content sidebar" "modal__footer modal__footer";
  grid-template-columns: 2fr 1fr;
  grid-row-gap: 5px;
  row-gap: 5px; }

.tour-slider {
  grid-area: slider; }

.tour-map {
  grid-area: map;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 10px; }
  .tour-map h1 {
    margin: 0 auto 0 5px; }

.tour-map span {
  font-family: GT Eesti ,Arial,sans-serif;
  font-weight: bold;
  font-size: 1.2em;
  margin-left: .2em;
  background-color: #ffd80c;
  max-width: 83%;
  padding: 10px 5px;
  display: block;
  border-radius: 5px;
  text-transform: uppercase; }

.tour-content {
  grid-area: content; }

.tour-sidebar {
  grid-area: sidebar;
  background: #e9f0fa;
  border-radius: 3px;
  clear: both; }
  .tour-sidebar h2 {
    padding-left: 10px;
    margin: 5px 0; }
  .tour-sidebar ul {
    padding-left: 10px; }
    .tour-sidebar ul li {
      display: block;
      list-style: none; }
      .tour-sidebar ul li i {
        margin-right: 10px;
        width: 16px; }
      .tour-sidebar ul li span {
        display: inline-block;
        padding-left: 2.7em;
        font-size: 11px;
        color: #a7a7a7; }

.tour-content, .tour-sidebar {
  border-top: solid 1px #ededed;
  border-bottom: solid 1px #ededed;
  padding: 10px;
  margin: 0 10px; }

.modal__footer {
  grid-area: modal__footer;
  padding: 10px;
  margin: 0 10px; }
  .modal__footer .extra-info {
    display: -webkit-flex;
    display: flex; }
    .modal__footer .extra-info details {
      margin-right: 2em; }
    .modal__footer .extra-info details > summary {
      outline: none !important; }
      .modal__footer .extra-info details > summary:hover {
        cursor: pointer; }
  .modal__footer ul {
    padding-left: 0; }
    .modal__footer ul li {
      list-style: none; }
      .modal__footer ul li i {
        margin-right: 10px;
        width: 16px; }
      .modal__footer ul li i[class*='check'] {
        color: #61c361; }
      .modal__footer ul li i[class*='times'] {
        color: #bd4646; }
  .modal__footer .contact-host {
    display: grid;
    grid-template-columns: 24em 10em;
    grid-template-rows: 30px;
    -webkit-align-items: center;
            align-items: center; }
    .modal__footer .contact-host button {
      background-color: #fff;
      color: #07c;
      border-color: #07c;
      border: 1px solid #000;
      border-radius: 2px;
      display: inline-block;
      padding: 8px 16px;
      cursor: pointer;
      text-decoration: none;
      box-sizing: border-box;
      vertical-align: middle;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 1.4285714286em; }
      .modal__footer .contact-host button:active {
        background-color: #cce4f5; }
      .modal__footer .contact-host button:focus {
        color: #07c; }
      .modal__footer .contact-host button:hover {
        background-color: #e5f0ff; }
